.context {
	margin-top: rem(65);
	margin-bottom: rem(160);

	a {
		color: var(--cornflower);

		&:hover {
			text-decoration: underline;
		}
	}

	p {
		line-height: rem(28);
		letter-spacing: -0.11px;
	}

	b {
		color: var(--dark-indigo);
		line-height: rem(28);
		letter-spacing: -0.11px;
		font-weight: var(--font-w-sm);
	}

	ol {
		
		counter-reset: section;
		list-style-type: none;
		margin-bottom: rem(24);

		li {
			position: relative;
			line-height: rem(28);
			letter-spacing: -0.11px;
			color: var(--slate);

			padding-left: rem(24);
			

			&::before {
				position: absolute;
				top: 0;
				left: 0;
				counter-increment: section;
				content: counters(section, ".") ".";

				color: var(--slate);
				// width: rem(24);

				margin-right: rem(10);
			}

			ol {
				padding-inline-start: 0;

				li {
					padding-left: rem(7);

					&::before {
						left: rem(-24);
					}
				}
			}

			a {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	ol.alpha {
		margin-left: rem(-10);

		li {
			list-style-type: lower-alpha;
			padding-left: rem(10);

			&::before {
				display: none;
			}

			ol {
				margin-top: rem(24);
				margin-left: rem(14);

				li {
					list-style-type: lower-roman;
				}
			}
		}
	}

	ol.mb-1 {
		li {
			margin-bottom: rem(24);

			&:last-child {
				margin-bottom: 0;
			}

			ol {
				margin-top: rem(24);
			}

			ul {
				margin-top: rem(24);
				li {
					// list-style: none;
					padding-left: rem(7);
					margin-left: rem(20);
					margin-bottom: 0;

					&::before {
						display: none;
					}
				}

				li.style-0 {
					list-style: none;

					
					p {
						margin-top: rem(24);
					}
				}
			}
		}
	}

	ul {
		margin-bottom: rem(24);
		li {
			list-style-type: disc;
			padding-left: rem(10);
			margin-left: rem(30);
			margin-bottom: rem(24);

			color: var(--slate);
			line-height: rem(28);
			letter-spacing: -0.11px;

			&:last-child {
				margin-bottom: 0;
			}

			b {
				color: var(--dark-indigo);
				font-weight: var(--font-w-sm);
			}
		}
	}

	ul.mb-0 {
		li {
			margin-bottom: 0;
		}
	}

	> u {
		display: block;
		margin-bottom: rem(24);
		letter-spacing: -0.11px;
		color: var(--slate);
	}

	> b, p {
		margin-bottom: rem(24);
		display: block;
	}
}