.page_account {
	background: url(../img/bg-image.png) no-repeat center center;
	background-size: cover;

	// height: 100vh;

	display: flex;
	justify-content: center;

	@include w_default {
		height: 100vh;
	}

	.container {
		width: 100%;
		display: flex;
		justify-content: center;
	}

	&__sing_in {
		width: rem(540);

		padding-bottom: rem(138);
	}

	&__sing_up {
		width: 100%;

		padding-bottom: rem(78);

		.page_account__form {
			width: rem(540);

			h2 {
				
			}

			.field_set {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				label {
					flex: 0 0 rem(136);
					max-width: rem(136);
					margin-bottom: 0;
				}

				.field,
				.custom-select {
					flex: 1 1 auto;
					width: auto;
				}
			}
		}
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__text {
		flex: 0 0 rem(520);
		max-width: rem(520);
		margin-right: rem(20);

		h1 {
			font-size: rem(48);
			color: var(--dark-indigo);
			margin-bottom: rem(24);
			margin-top: rem(20);
		}

		p {
			font-size: rem(18);
		}
	}

	&__header {
		padding-top: rem(40);
		margin-bottom: rem(35);
	}

	&__form {
		border-radius: rem(8);
		background-color: white;
		box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.12),
		0 22px 24px -5px rgba(0, 0, 0, 0.08);
		padding: rem(48) rem(70) rem(53);

		h1,h2 {
			font-size: rem(25);
			line-height: rem(49);
			letter-spacing: -0.15px;
			margin-bottom: rem(24);
			font-weight: var(--font-w-sm);
			color: var(--slate-two);
		}

		.field_descr {
			margin-bottom: rem(24);
			p {
				font-size: rem(14);
				letter-spacing: -0.08px;
				color: var(--slate);

				a {
					color: var(--cornflower);

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		.field_required {
			display: flex;
			margin-bottom: rem(16);

			p {
				font-size: rem(14);
				letter-spacing: -0.08px;
				color: var(--slate);
			}

			&::before {
				content: "";
				flex: 0 0 rem(136);
				max-width: rem(136);
			}
		}

		.field_set {
			margin-bottom: rem(24);

			label {
				font-weight: var(--font-w-sm);
				color: var(--slate-two);
				margin-bottom: rem(16);
				display: block;
			}

			a {
				color: var(--cornflower);
				font-size: rem(15);
				font-weight: var(--font-w-sm);

				&:hover {
					text-decoration: underline;
				}
			}

			
		}

		.field_text {
			display: flex;
			justify-content: center;

			p {
				color: var(--slate-two);

				a {
					color: var(--cornflower);

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		.field {
			height: rem(48);
			border-radius: rem(8);
			width: 100%;
			border: rem(1) solid #e6eaef;
			padding: 0 rem(14);

			color: var(--steel);
			font-size: rem(16);

			transition: all 200ms ease;

			&:focus {
				border-color: var(--cornflower);
			}

			@include placeholder {
				color: var(--steel);
			}
		}

		.field_checked {
			// input {
			// 	display: none;
			// }

			display: flex !important;
			align-items: center !important;

			p {
				margin-left: rem(16);
				font-size: rem(15);
				font-weight: var(--font-w-sm);
				color: var(--slate-two);
			}
		}

		.button_form {
			height: rem(48);
			width: 100%;
			background-color: var(--cornflower);
			display: flex;
			align-items: center;
			justify-content: center;
			color: white;
			border-radius: rem(8);
			font-weight: var(--font-w-sm);

			i {
				margin-left: rem(14);
				font-size: rem(10);
				margin-top: rem(1);
			}
		}
	}
}