.header {
	background: #fcfafb url(../img/bg-image.png) no-repeat bottom center;
	background-size: cover;

	&__top {
		padding-top: rem(14);
		

		margin-bottom: rem(76);

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			
		}

		&-logo {
			margin-right: rem(88);
			margin-top: rem(10);
		}

		&-nav {

			> ul {

				display: flex;
				flex-wrap: wrap;
				// align-items: center;

				> li {
					position: relative;
					margin-right: rem(40);

					&:last-child {
						margin-right: 0;
					}

					> a {
						color: var(--dark-indigo);
						font-weight: var(--font-w-sm);

						display: flex;
						align-items: center;

						transition: all 200ms ease;

						&:hover {
							color: var(--cornflower);
						}

						i {
							pointer-events: none;
							margin-top: rem(2);
							margin-left: rem(16);
							font-size: rem(6);
						}
					}

					.dropmenu {
						position: absolute;
						z-index: 10;
						top: rem(41);
						left: 0;
						padding: rem(20);
						border-radius: rem(2);
						background-color: white;
						box-shadow: 0 10px 32px -5px #e1a7e8;

						display: none;

						&::before {
							content: "";
							position: absolute;
							top: rem(-8);
							left: 50%;
							margin-left: rem(-4);
							border: rem(8) solid transparent;
							border-bottom-color: white;
							border-top: 0;
							width: 0;
							height: 0;
							
						}

						li {

							a {
								line-height: rem(30);
								white-space: nowrap;
								transition: all 200ms ease;
								color: var(--slate);

								&:hover {
									color: var(--cornflower);
								}
							}
						}
					}
				}
				> li.active_menu {
					.dropmenu {
						display: block;
					}
				}
			}
		}

		&-right {
			margin-left: auto;
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include wm_default {
				margin-right: rem(-80);
			}

			@include w_default {
				margin-right: rem(-120);
			}

			.first {
				margin-right: rem(30);

				font-weight: var(--font-w-sm);
				color: var(--cornflower);
			}
		}
	}

	&__bottom {
		overflow: hidden;
		display: grid;
		grid-template-columns: calc((100% - var(--w-container))/2) 6.5fr 3.5fr calc((100% - var(--w-container))/2);

		h1 {
			font-size: rem(48);
			font-weight: var(--font-w-sm);
			color: var(--dark-indigo);
		}

		&-left {
			// max-width: rem(682);
			// flex: 0 0 rem(682);

			grid-column: 2 / span 1;
			// grid-column: auto;

			h1, p, .button-default {
				margin-bottom: rem(24);
			}

			

			.descr {
				font-size: rem(18);
				max-width: rem(590);
			}

			.info {
				font-size: rem(13);

				a {
					color: var(--cornflower);
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}

			// .button-default {
				
			// }
		}

		&-right {
			position: relative;
			
			grid-column-end: span 2;

			

			img {
				&:first-child {
					position: absolute;
					top: 0;	
					left: rem(90);

					width: 100%;
					// height: 100%;
					// object-fit: cover;
					// object-position: top left;
					// z-index: -1;
				}

				&:last-child {
					position: relative;
					top: 0;
					left: rem(-45);
					z-index: 1;
					margin-top: rem(63);
				}
			}

			.static {
				position: relative !important;
				top: auto !important;
				left: auto !important;
				margin-top: 0 !important;
			}
		}
	}

	&__bottom_static {
		display: flex;
		flex-wrap: wrap;

		max-width: var(--w-container);
		width: 100%;
		margin-left: auto;
		margin-right: auto;
	}

	&__bottom-col {
		display: flex;
		overflow: visible;

		padding-bottom: rem(180);

		.container {
			display: flex;
			width: 100%;
			flex-direction: column;
		}

		&__text {
			margin-bottom: rem(64);

			h1 {
				margin-bottom: rem(24);
			}

			p {
				font-size: rem(18);
			}
		}

		&__items {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.item {
				flex: 0 0 rem(530);
				max-width: rem(530);
				border-radius: rem(8);
				background-color: white;
				box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.12),
				0 22px 24px -5px rgba(0, 0, 0, 0.08);

				padding: rem(32);

				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				&__left {
					flex: 0 0 rem(203);
					max-width: rem(203);

					b, strong {
						font-size: rem(28);
						font-weight: var(--font-w-sm);
						color: var(--dark-indigo);
						margin-bottom: rem(13);
						display: block;
					}

					p {
						line-height: rem(23);
						letter-spacing: -0.11px;
						margin-bottom: rem(24);
					}

					.button-default {
						width: rem(148);
						display: flex;
						align-items: center;

						i {
							margin-left: auto;
							margin-top: rem(2);
						}
					}
				}

				&__right {
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;

					flex: 0 0 rem(223);
					max-width: rem(223);

					b, strong {
						font-size: rem(28);
						font-weight: var(--font-w-sm);
						color: var(--slate);
						margin-bottom: rem(8);
					}

					span {
						color: var(--bluey-grey);
						line-height: rem(23);
						letter-spacing: -0.11px;

						&:first-of-type {
							margin-bottom: rem(8);
						}
					}
				}
			}
		}
	}

}

.header_static {
	background: white;

	.header__top {
		margin-bottom: 0;
		background-color: white;
		height: rem(80);
		box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
	}

	.header__about {
		height: rem(240);

		.container {

			p {
				max-width: rem(530);
			}
		}
	}

	.header__blog {
		background: url(../img/hero-bg.jpg) no-repeat top center;
		background-size: cover;
		height: rem(220);

		.container {
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;

			h1 {
				font-size: rem(48);
				font-weight: var(--font-w-sm);
				color: white;
				margin-bottom: rem(16);
			}

			p {
				font-size: rem(18);
				color: white;
				opacity: .8;
			}
		}
	}
}

.header_white {
	background-color: white;
}

#header_overview {
	background-color: white;

	.header__bottom {
		grid-template-columns: calc((100% - 1080px)/2) 6fr 4fr calc((100% - 1080px)/2);

		&-right {
			img {
				&:last-child {
					left: rem(-50);
				}
			}
		}
	}
}

.how_to_create_invoice {
	overflow: visible;
	background-color: white;
	justify-content: space-between;

	padding-bottom: rem(147);

	.header__bottom {
		overflow: visible;
	}

	.header__bottom-left {
		flex: 0 0 rem(682);
		max-width: rem(682);
	}

	.header__bottom-right {
		flex: 0 0 rem(357);
		max-width: rem(357);

		img {
			left: rem(30) !important;

			@include w_default {
				left: rem(80) !important;
			}
		}
	}
}