.sample_templates {
	padding-top: rem(80);
	padding-bottom: rem(64);
	margin-bottom: rem(80);

	background-color: #fcfafb;

	h2 {
		margin-bottom: rem(24);
	}

	p {
		font-size: rem(20);
		line-height: rem(28);
		letter-spacing: -0.14px;
	}

	.flex {
		margin-top: rem(62);

		img {
			overflow: hidden;
			border-radius: rem(6);

			width: rem(200);

			margin-right: rem(20);

			&:last-child {
				margin-right: 0;
			}
		}
	}
}