.receipt {

	padding-bottom: rem(80);

	&__header {
		max-width: rem(646);
		margin-bottom: rem(70);
	}

	.flex {

		.column_left {
			flex: 0 0 rem(535);
			max-width: rem(535);
			margin-right: rem(20);
		}	

		.column_right {
			
			flex: 0 0 rem(520);
			max-width: rem(520);
		}
	}
}

.media_block {
	border-radius: rem(16);
	background-color: var(--pale-grey);
	padding: rem(37) rem(37) rem(32) rem(37);

	display: flex;
	flex-direction: column;
	align-items: center;

	&__content {
		border-radius: rem(4);
		height: rem(250);
		width: 100%;
		overflow: hidden;
	}

	p {
		margin-top: rem(32);
	}

	// iframe {

	// }
}