.about_help {
	margin-top: rem(80);
	margin-bottom: rem(80);

	.container {
		position: relative;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__left, &__right {
		flex: 0 0 rem(520);
		max-width: rem(520);
	}

	&__left {
		h2,h3 {
			margin-bottom: rem(24);
		}

		p {
			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
		}
	}

	&__right {
		position: absolute;
		top: -50%;
		right: 0;
	}
}