.partner {
	padding-bottom: rem(76);
	margin-bottom: rem(84);
	background-color: #fcfafb;

	&__header {
		display: flex;
		flex-direction: column;

		h2 {
			margin-bottom: rem(24);
		}

		p {
			margin-bottom: rem(26);
			max-width: rem(531);

			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 2.44rem) / 4);
			margin-right: rem(13);

			height: rem(130);

			@include flex-center;

			&:nth-child(4n) {
				margin-right: 0;
			}
		}
	}
}