.article_cols {
	margin-bottom: rem(160);
}

.article_row {
	padding: rem(80) 0;

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&:first-child {
		padding-top: 0;
	}

	&:nth-child(2n) {
		background-color: var(--pale-grey);
	}

	&__left,
	&__right {
		flex: 0 0 rem(520);
		max-width: rem(520);

		h2,
		h3 {
			margin-bottom: rem(24);
		}

		p {
			&:first-of-type {
				font-size: rem(20);
				line-height: rem(28);
				letter-spacing: -0.14px;

				margin-bottom: rem(24);
			}

			&:last-of-type {
				line-height: rem(24);
				letter-spacing: -0.14px;
			}
		}

		.button-default {
			margin-top: rem(32);
		}
	}
	
	&__left {
		
	}
}