.quickly {
	background-color: var(--pale-grey);
	padding: rem(80) 0;

	.flex {

		.column_left {
			flex: 0 0 rem(436);
			max-width: rem(436);
			margin-right: rem(104);

			
		}

		.column_right {
			flex: 0 0 rem(540);
			max-width: rem(540);

			.accordion {
				margin-bottom: rem(89);
			}

			.button-default {

			}
		}
	}

	h2, h3 {
		margin-bottom: rem(80);
	}
}

.quickly.quickly_pricing_page {
	background-color: white;

	.column_right {
		h2, h3 {
			margin-bottom: rem(24);
		}

		> p {
			&:first-of-type {
				font-size: rem(20);
				line-height: rem(28);
				letter-spacing: -0.14px;
				margin-bottom: rem(24);
			}
			&:last-of-type {
				line-height: rem(24);
				margin-bottom: rem(24);
			}
		}

		> .flex {
			.button-default {
				margin-right: rem(16);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}