.questions {
	margin-bottom: rem(162);
	margin-top: rem(80);

	&__header {
		text-align: center;
		margin-bottom: rem(80);
	}
}

.accordion {

	&__item {
		margin-bottom: rem(24);
		padding-bottom: rem(24);
		border-bottom: rem(1) solid #f2e9f0;

		&:last-child {
			margin-bottom: 0;
		}

		i {
			width: rem(16);
			height: rem(16);
			margin-right: rem(16);
		}

		&-icon {
			background: url(../img/plus.svg) no-repeat;
			background-size: 100%;
		}

		&-button {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			b {
				font-size: rem(18);
				color: var(--dark-indigo);
				font-weight: var(--font-w-sm);
			}
		}

		&-content {
			padding-top: rem(24);
			// padding-bottom: rem(24);

			display: none;

			p {
				line-height: rem(24);
				letter-spacing: 0.14px;
			}
		}
	}

	&__item.active {

		.accordion__item-icon {
			background: url(../img/minus.svg) no-repeat;
			background-size: 100%;
		}

		.accordion__item-content {
			display: block;
		}
	}
}