.button-default {
	border-radius: rem(8);
	padding: rem(9) rem(16) rem(11) rem(19);
	background-color: var(--cornflower);

	color: white;
	font-weight: var(--font-w-sm);

	display: inline-block;

	// display: flex;
	// align-items: center;

	i {
		vertical-align: middle;
		margin-left: rem(14);

		font-size: rem(8);

		color: white;

		&::before {
			color: white;
		}
	}
}

.button-default.p-2 {
	padding: rem(13) rem(16) rem(15) rem(19);
}

.button-red {
	background-color: var(--orange-pink);
}
.button-gray {
	background-color: var(--pale-grey);
	color: var(--cornflower);

	i {
		color: var(--cornflower);

		&::before {
			color: var(--cornflower);
		}
	}
}



.approve-list {

	li {

		display: flex;
		align-items: center;

		font-size: rem(14);
		line-height: rem(24);
		font-weight: bold;
		color: var(--dark-indigo);

		&::before {
			content: "\e902";
			font-family: 'icons';
			font-size: rem(16);
			color: var(--cornflower);

			margin-right: rem(8);
		}

		// a {
			
		// }
	}
}

.language_select {
	display: flex;
	flex-direction: column;

	b {
		color: var(--dark-indigo);
		font-weight: var(--font-w-sm);
		margin-bottom: rem(19);
	}

	&__list {
		position: relative;
		overflow: hidden;
		width: rem(200);
		background-color: white;

		.selected {
			width: 100%;
			border-radius: rem(6);
			border: rem(1) solid #e6eaef;
			padding: rem(11) rem(12);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			&::after {
				content: "\e914";
				font-size: rem(6);
				font-family: 'icons';

				margin-left: auto;
			}

			img {
				margin-right: rem(12);
			}

			span {
				color: var(--dark-indigo);
			}
		}
	}
}


.nav_tabs {

	ul {
		display: flex;
		flex-wrap: wrap;

		li {
			margin-right: rem(8);


			&:last-child {
				margin-right: 0;
			}

			a {
				border-radius: rem(8);
				padding: rem(9) rem(16) rem(11) rem(16);
				background-color: transparent;

				font-weight: var(--font-w-sm);
				color: var(--dark-indigo);

				transition: all 200ms ease;

				&:hover {
					background-color: rgba(99, 92, 255, 0.1);
					color: var(--cornflower);
				}
			}

			a.active {
				background-color: rgba(99, 92, 255, 0.1);
				color: var(--cornflower);
			}
		}
	}
}

.count-list {
	display: flex;
	flex-direction: column;

	counter-reset: section;
	list-style-type: none;

	li {
		position: relative;
		margin-bottom: rem(24);
		padding-left: rem(48);
		padding-bottom: rem(24);
		border-bottom: rem(1) solid #f2e9f0;

		// display: flex;
		// flex-wrap: wrap;

		&:last-child {
			margin-bottom: 0;
		}

		&::before {
			position: absolute;
			top: rem(-6);
			left: 0;

			counter-increment: section;
			content: counter(section);

			width: rem(32);
			height: rem(32);
			border-radius: 50%;
			color: var(--purpleish-pink);
			font-size: rem(18);
			font-weight: var(--font-w-sm);

			margin-right: rem(16);

			@include flex-center;

			background-image: linear-gradient(45deg, rgba(180, 138, 231, .15), rgba(246, 130, 132, .15) 100%);
		}

		color: var(--dark-indigo);

		b,
		strong {
			font-weight: var(--font-w-sm);
		}

		
	}
}

.tile_col {
	display: flex;
	flex-direction: column;

	&__item {
		width: 100%;
		height: rem(85);
		background-color: white;

		display: flex;
		flex-direction: column;
		justify-content: center;

		&:nth-child(2n) {
			// background-color: red;
			background-image: linear-gradient(to left, var(--pale-grey), #ffffff, #ffffff);
		}

		&-text {
			display: flex;
			flex-direction: column;
			justify-content: center;

			b {
				font-weight: var(--font-w-sm);
				letter-spacing: -0.09px;
				color: var(--dark-indigo);
				margin-bottom: rem(8);
			}

			p {
				letter-spacing: -0.09px;
				max-width: rem(520);
			}
		}

		&-row {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			img {
				margin-right: rem(16);
			}

			b, strong {
				font-weight: var(--font-w-sm);
				color: var(--dark-indigo);
				letter-spacing: -0.09px;
			}
		}
	}
}

.tile_col_block {

	&__header {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(24);

		.item {
			flex: 0 0 50%;
			max-width: 0 0 50%;
		
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			img {
				margin-bottom: rem(24);
			}

			b, strong {
				font-weight: var(--font-w-sm);
				color: var(--dark-indigo);
				letter-spacing: -0.09px;
			}
		}
	}

	&__wrap {
		border-radius: rem(8);
		box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.12),
			0 22px 24px -5px rgba(0, 0, 0, 0.08);
		padding: rem(4);
	}

	&__row {

		display: flex;
		flex-wrap: wrap;
		height: rem(85);

		&:nth-child(2n) {
			// background-color: red;
			background-color: var(--pale-grey);
		}

		.item {
			flex: 0 0 50%;
			max-width: 50%;

			padding: rem(7) rem(20);
			margin: rem(-7) 0;

			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			&:first-child {
				border-right: rem(1) solid #f2e9f0;
			}

			b,
			strong {
				font-weight: var(--font-w-sm);
				color: var(--slate);
				letter-spacing: -0.09px;

				margin-bottom: rem(2);
			}

			p {
				font-size: rem(14);
				color: var(--bluey-grey);
				letter-spacing: -0.08px;
				text-align: center;
			}

			.flex-col {
				align-items: center;
			}
		}

		.item.item-center {
			align-items: center;
		}
		.item.item-center.flex-col {
			img {
				margin-bottom: rem(6);
			}
		}

		.item.item-bs {
			margin-top: rem(6);
		}

	}

}