:root {
	--font-default: 'Source Sans Pro';
	--font-w-l: 300;
	--font-w-sm: 600;

	--dark-indigo: #0a2540;
	--slate: #425566;
	--cornflower: #635cff;
	--purpleish-pink: #e66ecb;
	--pale-grey: #fcfafb;
	--bluey-grey: #8c9fb1;
	--orange-pink: #ff6a5c;
	--slate-two: #3c4357;
	--steel: #738096;

	--w-container: 1080px;
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
	font-size: rem(16);
	padding: 0;
}

a {
	text-decoration: none;
}

p {
	color: var(--slate);
}

h2,h3 {
	font-size: rem(43);
	line-height: rem(49);
	letter-spacing: -0.25px;
	color: var(--dark-indigo);
}

li {
	list-style: none;
}

body {
	position: relative;
	font-size: rem(16);
	font-family: var(--font-default);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	max-width: var(--w-container);
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}