.footer {
	background-color: #f5f0f4;
	padding-top: rem(92);
	padding-bottom: rem(83);

	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-right: rem(-80);
		margin-bottom: rem(80);

		&-left {
			
			.logo {
				margin-bottom: rem(32);
			}

		}

		&-right {
			display: flex;
			flex-wrap: wrap;


		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-bottom: rem(73);

		&-left {
			a {
				margin-right: rem(8);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			p {
				font-size: rem(14);
				font-weight: var(--font-w-sm);
			}
			
			.social {
				margin-left: rem(16);
			}
		}
	}

	&__copy {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		a, p {
			font-size: rem(14);
		}

		a {
			position: relative;
			color: var(--cornflower);

			margin-right: rem(6);

			&:hover {
				color: var(--dark-indigo);
			}

			&::after {
				content: "-";
				font-size: rem(14);
				color: var(--slate);
				margin-left: rem(6);

				pointer-events: none;
			}

			&:last-of-type {
				&::after {
					display: none;
				}
			}
		}

		p {
			margin-left: rem(24);
		}

	}
}

.social {
	display: flex;
	flex-wrap: wrap;

	a {
		margin-right: rem(16);

		i {
			font-size: rem(24);
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.nav_col {
	width: rem(200);
	display: flex;
	flex-direction: column;

	&__title {
		font-size: rem(16);
		line-height: rem(30);
		font-weight: var(--font-w-sm);
		color: var(--dark-indigo);
		text-transform: uppercase;

		margin-bottom: rem(34);
	}

	ul {

		li {

			a {
				line-height: rem(30);
				color: var(--dark-indigo);

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}