.group_list {
	margin-bottom: rem(80);

	&__header {
		margin-bottom: rem(86);

		max-width: rem(800);

		p {
			margin-top: rem(24);
			font-size: rem(20);
			line-height: rem(28);
			letter-spacing: -0.14px;
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 10rem) / 4);
			margin-right: rem(53);

			// display: flex;
			// flex-direction: column;

			img, b, p {
				display: block;
			}

			img {
				// width: 100%;
				height: rem(38);
				margin-bottom: rem(16);
			}
			

			b {
				font-weight: var(--font-w-sm);
				color: var(--dark-indigo);
				margin-bottom: rem(8);
			}

			p {
				line-height: rem(24);
				letter-spacing: 0.14px;
			}

			&:nth-child(4n) {
				margin-right: 0;
			}

			// &:nth-child(-n+4) {
			// 	margin-bottom: rem(80);
			// }
		}
	}

	.group-col {

		.item {
			&:nth-child(-n+4) {
				margin-bottom: rem(80);
			}
		}
		
	}

	.items-3 {
		.item {
			width: calc((100% - 5.625rem) / 3);

			margin-right: rem(45);

			&:nth-child(4n) {
				margin-right: rem(45);
			}

			&:nth-child(3n) {
				margin-right: 0;
			}
		}
	}

	&__button {
		margin-top: rem(84);
		margin-bottom: rem(20);
		display: flex;
		justify-content: center;

	}
}

.group_list.estimates {
	.group_list__header {
		max-width: rem(623);
	}
}

#accept_payment_page {

	margin-bottom: rem(104);

	.group_list__header {
		max-width: rem(922);
	}
}